.dp-po-post-content {
  font: $font-size-base $font-family-sans-serif;
  line-height: $line-height-base;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;

  p,
  ul,
  ol {
    margin-bottom: 1.5rem;
  }

  img {
    max-width: 100%;
  }

  p:last-child {
    margin: 0;
  }
}
