.dp-po-community-tabs {
  $self: '.dp-po-community-tabs';

  &-list {
    margin: 0;
    padding: 0;
  }

  &-item {
    display: inline-block;
    vertical-align: middle;
    list-style: none;
    margin-left: -4px;
    position: relative;

    &.active {
      #{$self}-link {
        border-top-color: $brand-primary;
        color: $text-color;
        background: $white;

        .dp-po-icon {
          color: $brand-primary;
        }
      }

      &:after {
        content: '';
        width: calc(100% - 2px);
        height: 2px;
        background: $white;
        position: absolute;
        bottom: -1px;
        left: 1px;
      }

      &:before {
        content: '';
        width: 100%;
        height: 2px;
        background: $brand-primary;
        position: absolute;
        top: -1px;
        left: 0;
        z-index: 1;
      }
    }

    &:first-child {
      margin-left: 0;
    }
  }

  &-link {
    background: $grey-lighter;
    border-left: 1px solid $grey-light;
    border-right: 1px solid $grey-light;
    padding: 13px 20px;
    color: $grey-dark;
    display: flex;
    align-items: center;
    justify-content: center;
    @include typo($s19, 400, 'head');

    .dp-po-icon {
      margin-right: 10px;
      display: flex;
      align-items: center;
      justify-content: center;
      font-size: 20px;
    }

    &:hover {
      text-decoration: none;
      color: $text-color;

      .dp-po-icon {
        color: $brand-primary;
      }
    }
  }

  &+.dp-po-block {
    position: relative;
  }
}
